import { Component, OnInit } from '@angular/core';

// IMPORTACAO DEPENDENCIAS LIB LEAFLET
import * as L from 'leaflet';

// IMPORTACAO DOS ARQUIVOS .JSON QUE SERAO UTILIZADOS PARA CARREGAMENTO DO MAPA
import data from './../../assets/files/campinasData.json'
import arrayCity from './../../assets/files/dataObject.json'

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})
export class MapComponent implements OnInit {

  private map
  private campinasData = data
  private arrayCityes = arrayCity

  constructor() { }

  ngOnInit() {
    //this.initSouthAmerica()
    this.initCampinas()
  }

  private initSouthAmerica(): void {
    this.map = L.map('map', {
      center: [ -22.9064, -47.0616 ],
      zoom: 3
    });

    const tiles = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      maxZoom: 10,
      attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>'
    });
    
    tiles.addTo(this.map);
  }


  public initCampinas() {
    this.map = L.map('map').setView([ -22.6970, -47.155], 9);
    L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token=pk.eyJ1IjoiYWxib3JkaWdub24iLCJhIjoiY2l0MzFlZXdzMHRyNjJvcGdnY2txZXdsMCJ9.IOAXYE1_mrHHHUbVMxR59Q', {
      maxZoom: 30,
      attribution: 'Map data &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors, ' +
        '<a href="http://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, ' +
        'Imagery © <a href="http://mapbox.com">Mapbox</a>',
      id: 'mapbox.streets'
    }).addTo(this.map);
  }


  desenhaCampinas(){
    var myStyle = {
        "color": "#ff7800",
        "weight": 1,
        "opacity": 0.15
    };

    L.geoJSON(this.campinasData, {
        style: myStyle
    }).addTo(this.map);
  }


  // CARREGA CIDADES DA REGIAO
  adicionaMarcador(){  

    // ITERAÇÃO PARA MARCAR TODAS A CIDADES DA GERIAO DE CAMPINAS A PARTIR 
    // DAS COORDENADAS OBTIDAS PELO ARQUIVO .JSON
    for(var i = 0; i < this.arrayCityes.infoCidades.length; i++){
      this.arrayCityes.infoCidades[i].resp =
      L.marker([this.arrayCityes.infoCidades[i].la, this.arrayCityes.infoCidades[i].lo]).addTo(this.map);
    }
  }	  


 // IMAGEM CONVERTIDA EM BASE64 DEVIDO A CONFLITOS OCORRIDOS, SERÁ REMOVIDA 
 adicionaProduto(){     
      let scooter = L.icon({
          iconUrl: 'data:image/jpeg;base64,',
          shadowUrl: 'data:image/jpeg;base64,',
          iconSize:     [30, 26], // size of the icon
          shadowSize:   [0, 0] // size of the shadow                
      });
          
      let p1 = L.marker([-22.8730, -47.0580], {icon: scooter}).addTo(this.map);
      let p2 = L.marker([-22.8741, -47.0431], {icon: scooter}).addTo(this.map);
      let p3 = L.marker([-22.8709, -47.0479], {icon: scooter}).addTo(this.map);
      let p4 = L.marker([-22.8705, -47.0479], {icon: scooter}).addTo(this.map);
    }    
  

}
